.catalog-sections {
  @include mobile-container;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 17px;
}

.catalog-sections__section-preview {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

@media (min-width: $tablet-width) {
  .catalog-sections {
    @include tablet-container;
    margin-bottom: 45px;
  }

  .catalog-sections__section-preview {
    margin-bottom: 30px;
  }
}

@media (min-width: $desktop-width) {
  .catalog-sections {
    @include desktop-container;
    justify-content: space-between;
    margin-bottom: 43px;
  }

  .catalog-sections__section-preview {
    &:last-child {
      margin-bottom: 30px;
    }

    &--slim {
      margin-right: auto;
    }
  }
}
